import Release from '../models/release';
import Track from '../models/track';

//@ts-ignore
import { MediaPlayer } from '@cassette/player';

import '@cassette/player/dist/css/cassette-player.css';

interface Props {
  release: Release
  includeSides: boolean
}

export default function ReleaseComponent (props: Props): JSX.Element {
  return (
    <div className='release'>
      <div className='release-title'>
        {props.release.name}
      </div>
      {renderReleaseInfo(props.includeSides, props.release.trackListing)}
      {renderMediaPlayer(props.release.trackListing)}
    </div>
  );  
}

function renderReleaseInfo(includeSides: boolean, tracks: Track[]): JSX.Element {
  const sectionLimit = 5; 

  const sections = tracks.reduce((sections: Track[][], item, index) => { 
    const chunkIndex = Math.floor(index/sectionLimit);

    if(!sections[chunkIndex]) {
      sections[chunkIndex] = [];
    }
    sections[chunkIndex].push(item);
    return sections;
  }, []);

  return (
    <div className='release-info'>
      {sections.map((tracks, index) => {
        return (
          <section key={index} className='tracks-section'>
            {includeSides && <div className='section-name'>
              Side {index + 1}
            </div>}
            {tracks.map(track => 
              <div className='track-info'>
                {track.name} - {track.artist}
              </div>
            )}
          </section>
        );
      })}      
    </div>
  );
}

function renderMediaPlayer(tracks: Track[]): JSX.Element {
  const playlist = tracks.map(track => {
    return { title: track.name, url: track.url }
  });

  return (
    <MediaPlayer
      playlist={playlist}
    />
  );
}

