import FooterComponent from '../components/footer';
import ReleaseComponent from '../components/release';
import Release from '../models/release';
import Track from '../models/track';

interface Props {
  tracks: Track[]
}

export default function NewMusicPage(props: Props): JSX.Element {
  const release: Release = {
    name: 'New Music',
    trackListing: props.tracks
  };

  return (
    <div className='new-music'>
      <ReleaseComponent release={release} includeSides={false}/>
      <FooterComponent />
    </div>
  );
}